





























import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})

export default class Profile extends Vue {
  private publicPath: any = process.env.BASE_URL;
  private ownerDetails: any = {
    name: 'Keanu Dela Cruz',
    jobTitle: 'Front-End Developer',
  }
  private showProfile: boolean = false;
  private index: any = 0;
  private images: any = [
    `${this.publicPath}2x2.jpg`
  ];

  private onChange(index: any) {
    this.index = index;
  }
}
